<script>
// @ is an alias to /src
import dayjs from "dayjs";
export default {
  filters: {
    date(val) {
      return dayjs(val).format("MM/DD");
    }
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".pagination-home",
          clickable: true
        }
      },
      newsCats: [],
      heroCats: []
    };
  },
  created() {
    this.fetchNewCats();
    this.fetchHeroCats();
  },
  methods: {
    async fetchNewCats() {
      const res = await this.$http.get("/news/list");
      this.newsCats = res.data;
    },
    async fetchHeroCats() {
      const res = await this.$http.get("/heroes/list");
      this.heroCats = res.data;
    }
  },
  components: {}
};
</script>
